<template>
  <div id="settleIn">
    <el-container class="header-bg">
      <header-top></header-top>
    </el-container>
    <div class="settle-in-container">
      <el-card class="box-card box-card-top" shadow="never"><strong>申请入驻</strong></el-card>
      <el-card class="box-card box-card-content" shadow="hover">
        <el-row>
          <el-col :span="22" :offset="1">
            <el-form :model="company" :rules="rules" label-width="150px" label-position="top" ref="companyForm">
              <el-form-item label="您的家政公司名称" prop="companyName" class="t-a-left">
                <el-input v-model="company.companyName" placeholder="请输入家政公司名称"></el-input>
              </el-form-item>
              <el-form-item label="公司管理员姓名" prop="managerName" class="t-a-left">
                <el-input v-model="company.managerName" placeholder="请输入公司管理员姓名"></el-input>
              </el-form-item>
              <el-form-item label="公司管理员电话" prop="phone" class="t-a-left">
                <el-input v-model="company.phone" placeholder="请输入公司管理员电话"></el-input>
              </el-form-item>
              <el-form-item label="公司所在城市" class="t-a-left" required>
                <el-col :span="24">
                  <el-form-item prop="province" class="t-a-left">
                    <el-select v-model="company.province" @change="changeProvince" placeholder="请选择所在省">
                      <el-option :label="item.name" :value="item.id + '-' + item.code" :key="item.id"
                        v-for="item in provinces"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <!-- <el-col :span="1"><div>-</div></el-col> -->
                <el-col :span="24">
                  <el-form-item prop="city" class="t-a-left">
                    <el-select v-model="company.city" @change="changeCity" placeholder="请选择所在市">
                      <el-option :label="item.name" :value="item.id + '-' + item.code" :key="item.id"
                        v-for="item in cities"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <!-- <el-col :span="1"><div>-</div></el-col> -->
                <el-col :span="24">
                  <el-form-item prop="country" class="t-a-left">
                    <el-select v-model="company.country" placeholder="请选择所在区">
                      <el-option :label="item.name" :value="item.code" :key="item.id"
                        v-for="item in countries"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label="公司具体地址" prop="addr" class="t-a-left">
                <el-input v-model="company.addr" placeholder="请输入公司具体地址"></el-input>
              </el-form-item>
              <el-form-item label="请上传公司营业执照" prop="businessLicence" class="t-a-left">
                <div class="image-note"><span>图片格式只支持：BMP、JPEG、JPG、PNG，大小不超过50M</span></div>
                <div>
                  <upload-image use="licence" image-size="50" @uploadSucc="uploadSuccess">
                    <img v-if="company.businessLicence" :src="company.businessLicence" class="avatar needsclick">
                    <div v-else>
                      <i class="el-icon-plus avatar-uploader-icon needsclick"></i>
                    </div>
                  </upload-image>
                </div>
              </el-form-item>
              <el-form-item label="请上传公司logo" prop="logo" class="t-a-left">
                <div class="image-note"><span>图片格式只支持：BMP、JPEG、JPG、PNG，大小不超过50M</span></div>
                <div>
                  <upload-image use="logo" image-size="50" @uploadSucc="uploadSuccess">
                    <img v-if="company.logo" :src="company.logo" class="avatar needsclick">
                    <div v-else>
                      <i class="el-icon-plus avatar-uploader-icon needsclick"></i>
                    </div>
                  </upload-image>
                </div>
              </el-form-item>
              <el-form-item class="t-a-left" label="请输入公司介绍" prop="decription">
                <!-- <quill-editor v-model="company.decription"
                  ref="myQuillEditor"
                  :options="editorOption">
                </quill-editor> -->
                <el-input type="textarea" :rows="6" maxlength="500" placeholder="请上传500字以内的公司介绍" v-model="company.decription"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" :disabled="sbIsdisabled" @click="submitCompanyInfo('companyForm')">提交入驻申请</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-card>
    </div>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 公司首次入驻功能
 *  */
import HeaderTop from './header-top/HeaderTop'
import UploadImage from '../components/UploadImage'
import { settleInRules, settleInCompany, makeParams, transformCompany, clearCompany } from './settleIn.rules'
import { promptBox } from '../service/common'
import { getLoginInfo } from '../assets/common/utils'
import { getProvinces, getCities, getCountries, saveCompanyInfo } from '../service/loginServer'
export default {
  name: 'settleIn',
  components: {
    'header-top': HeaderTop,
    'upload-image': UploadImage
  },
  data () {
    return {
      company: settleInCompany,
      rules: settleInRules,
      provinces: [],
      cities: [],
      countries: [],
      editorOption: {},
      succNote: '您的申请审核已提交，得贤工作人员将会在1-3个工作日内与您沟通进行审核，请耐心等待。',
      ossHost: ''
    }
  },
  computed: {
    sbIsdisabled () {
      let flag = true
      let {
        companyName,
        managerName,
        phone,
        province,
        city,
        country,
        addr,
        logo,
        businessLicence,
        decription
      } = this.company
      if (companyName && managerName && phone && province && city && country &&
        addr && logo && businessLicence && decription) {
        flag = false
      }
      return flag
    }
  },
  beforeRouteEnter (to, from, next) {
    let companyId = getLoginInfo('company_id')
    let company = getLoginInfo('company_object')
    if (!companyId || (company && company.audit_status !== 'approved')) {
      next()
    } else {
      next('/app')
    }
  },
  created () {
    let company = getLoginInfo('company_object')
    if (company) {
      this.fetchCompany(company)
    } else {
      console.log(clearCompany())
      this.company = clearCompany()
      this.fetchProvinces()
    }
  },
  methods: {
    fetchProvinces () {
      getProvinces().then(res => { this.provinces = res.data })
    },
    changeProvince (province) {
      let id = province.split('-')[0]
      this.company.city = ''
      this.company.country = ''
      getCities(id).then(res => { this.cities = res.data })
    },
    changeCity (city) {
      let id = city.split('-')[0]
      this.company.country = ''
      getCountries(id).then(res => { this.countries = res.data })
    },
    uploadSuccess (data) {
      this.ossHost = data.host
      if (data.use === 'licence') {
        this.company.businessLicence = data.path
      } else {
        this.company.logo = data.path
      }
    },
    submitCompanyInfo (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = makeParams(this.company, this.ossHost)
          saveCompanyInfo(params).then(res => {
            promptBox('友情提示', this.succNote, '退 出')
              .then(res => {
                if (res === 'confirm') {
                  sessionStorage.clear()
                  this.$router.replace('/login')
                }
              })
          })
        }
      })
    },
    fetchCompany (info) {
      let company = info
      getProvinces().then(res => {
        this.provinces = res.data
        return getCities(company.province_info.id)
      }).then(res => {
        this.cities = res.data
        return getCountries(company.city_info.id)
      }).then(res => {
        this.countries = res.data
        // 根据返回值转换数据对象
        this.company = transformCompany(company)
        console.log(this.company)
      })
    }
  }
}
</script>
<style scoped>
@import "../assets/styles/settleIn.css";
.t-a-left {
  text-align: left;
}
.el-select {
  display: inline-block;
  position: relative;
  width: 100%;
  margin-top: 20px;
}
.avatar {
  object-fit: cover;
}
</style>



