<template>
  <el-row class="headerTop" :gutter="0">
    <el-col :span="9" class="title">
      <div class="back" @click="goback">
        <i class="el-icon-arrow-left"></i>
      </div>
      <div class="home" @click="goHome" v-if="this.$route.name !== 'settleIn'">
        <i class="fa fa-home"></i>&nbsp;<font>首页</font>
      </div>
    </el-col>
    <el-col :span="15" class="text-left">
      <div class="admin-name">
        <p>&nbsp;&nbsp;您好, {{ name }}</p>
        <p>{{ nowTime }}</p>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import header from '../../assets/images/1.jpg'
import { getLoginInfo, getFormate } from '../../assets/common/utils.js'
import event from '../../event.js'
import { loginout } from '../../service/loginServer'
export default {
  data () {
    return {
      date: '',
      nowTime: '',
      roleName: {
        admin: '超级管理员'
      },
      manageName: '',
      userHeaderImg: header
    }
  },
  mounted () {
    event.$on('updateUserHeader', params => {
      this.userHeaderImg = getLoginInfo('url')
      this.$forceUpdate()
    })
    event.$on('updateUserName', params => {
      this.name = params
      this.$forceUpdate()
    })
  },
  created () {
    this.getDate()
    this.name = getLoginInfo('real_name') || getLoginInfo('account_number')
    let header = getLoginInfo('url')
    if (header && header !== 'null') {
      this.userHeaderImg = getLoginInfo('url')
    }
  },
  methods: {
    // 获取顶部的时间
    getDate () {
      this.nowTime = getFormate()
    },
    logout () {
      loginout().then(res => {
        sessionStorage.clear()
        this.$router.replace('/')
      })
    },
    goback () {
      this.$router.go(-1)
    },
    goHome () {
      this.$router.replace({ name: 'home' })
    }
  }
}
</script>

<style scoped="scoped">
.user-note {
  color: white;
  text-decoration-line: none;
}
.headerTop {
  height: 60px;
  color: #fff;
  text-align: center;
  width: 100%;
  /* position: fixed; */
  top: 0;
}
.title {
  display: flex;
  text-align: left;
}
.back {
  display: flex;
  align-items: center;
  height: 60px;
  width: 30px;
  margin-left: 20px;
  overflow: hidden;
  font-size: 20px;
}
.home {
  display: flex;
  align-items: center;
  height: 60px;
  font-size: 20px;
  margin-left: 5px;
}
.home font {
  font-size: 14px;
}
.title h3 {
  display: inline-block;
  color: #fff;
  font-weight: 400;
  margin: 0;
  vertical-align: 20px;
  cursor: pointer;
  margin-left: 15px;
}
.dateShow {
  width: 80%;
  margin: 0 auto;
  background: #3d566e;
  color: #fff;
  text-align: left;
  padding-left: 10px;
  border-radius: 50px;
  height: 40px;
  margin-top: 8px;
  line-height: 40px;
}
.text-left {
  text-align: right;
  padding-right: 20px;
}
.logout {
  display: block;
  width: 100%;
  height: 100%;
}
.marLt {
  margin-left: 30px;
}
.admin-name {
  display: inline-block;
  vertical-align: top;
  margin-top: 10px;
  /* margin-right: 10px; */
}
.admin-name p {
  margin: 0;
  color: #fff;
  cursor: default;
  font-size: 14px;
  line-height: 20px;
}
.roles {
  line-height: 16px;
  margin: 0;
  color: #fff;
  font-size: 14px;
  text-align: center;
}
.el-dropdown {
  text-align: center;
}
.toux img {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  display: block;
  margin: 0 auto;
  margin-top: 4px;
}
</style>



