/**
 * @author 吴永军
 * @description 公司入驻公司信息表单规则
 *  */
const PHONENUMBER = /^1(3|4|5|6|7|8|9)\d{9}$/

let validatePhoneRequired = (rule, value, callback) => {
  if (value === null || value === '') {
    callback(new Error('请输入公司管理员电话'))
  } else if (value && !PHONENUMBER.test(value)) {
    callback(new Error('请输入格式正确的手机号码'))
  } else {
    callback()
  }
}

export const settleInRules = {
  companyName: [
    { required: true, message: '请输入家政公司名称', trigger: 'blur' }
  ],
  managerName: [
    { required: true, message: '请输入公司管理员姓名', trigger: 'blur' }
  ],
  phone: [
    { required: true, validator: validatePhoneRequired, trigger: 'blur' }
  ],
  province: [
    { required: true, message: '请选择公司所在省份', trigger: 'change' }
  ],
  city: [
    { required: true, message: '请选择公司所在城市', trigger: 'change' }
  ],
  country: [
    { required: true, message: '请选择公司所在区县', trigger: 'change' }
  ],
  addr: [
    { required: true, message: '请输入公司具体地址', trigger: 'blur' }
  ],
  businessLicence: [
    { required: true, message: '请上传公司营业执照照片', trigger: 'blur' }
  ],
  logo: [
    { required: true, message: '请上传公司logo', trigger: 'blur' }
  ],
  decription: [
    { required: true, message: '请上传500字以内的公司介绍', trigger: 'blur' }
  ]
}

export const settleInCompany = {
  id: -1,
  companyName: '',
  managerName: '',
  phone: '',
  province: '',
  city: '',
  country: '',
  addr: '',
  logo: '',
  businessLicence: '',
  decription: ''
}

export const makeParams = (data, ossHost) => {
  let params = {}
  if (data.id !== -1) {
    params['id'] = data.id
  }
  params['title'] = data.companyName
  params['admin_name'] = data.managerName
  params['admin_phone'] = data.phone
  params['province_code'] = data.province.split('-')[1]
  params['city_code'] = data.city.split('-')[1]
  params['cd_code'] = data.country
  params['addr_info'] = data.addr
  params['logo'] = data.logo.split(ossHost)[1]
  params['business_license_img'] = data.businessLicence.split(ossHost)[1]
  params['remark'] = data.decription
  return params
}

export const transformCompany = data => {
  let result = {}
  result['id'] = data.id
  result['companyName'] = data.title
  result['managerName'] = data.admin_name
  result['phone'] = data.admin_phone
  result['province'] = `${data.province_info.id}-${data.province_code}`
  result['city'] = `${data.city_info.id}-${data.city_code}`
  result['country'] = data.cd_code
  result['addr'] = data.addr_info
  result['logo'] = data.logo
  result['businessLicence'] = data.business_license_img
  result['decription'] = data.remark
  return result
}

export const clearCompany = () => {
  let company = {
    id: -1,
    companyName: '',
    managerName: '',
    phone: '',
    province: '',
    city: '',
    country: '',
    addr: '',
    logo: '',
    businessLicence: '',
    decription: ''
  }
  return company
}



